html {
   box-sizing: border-box;
}

*,
*::before,
*::after {
   box-sizing: inherit;
}

.page {
   height: 100%;
   font-family: var(--font-family, sans-serif);
   font-weight: 400;
   -webkit-text-size-adjust: 100%;
}

.page__body {
   margin: 0;
   font-size: 16px;
}

img {
   height: auto;
   max-width: 100%;
   object-fit: cover;
}

a {
   text-decoration: none;
}

.site-container {
   overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
   display: none !important;
   /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
   border: none;
   padding: 0;
   background: transparent;
   cursor: pointer;
}

.list-reset {
   list-style: none;
   margin: 0;
   padding: 0;
}



.input-reset {
   -webkit-appearance: none;
   appearance: none;
   border: none;
   border-radius: 0;
   background-color: #fff;

   &::-webkit-search-decoration,
   &::-webkit-search-cancel-button,
   &::-webkit-search-results-button,
   &::-webkit-search-results-decoration {
      display: none;
   }
}

.visually-hidden {
   position: absolute;
   overflow: hidden;
   margin: -1px;
   border: 0;
   padding: 0;
   width: 1px;
   height: 1px;
   clip: rect(0 0 0 0);
}

.container {
   margin: 0 auto;
   padding: 0 var(--container-offset);
   max-width: var(--container-width);
}

.btn {
   padding: 12px 28px;
   margin: 0px auto;
   font-size: 18px;
   line-height: 23px;
   text-align: center;
   letter-spacing: 0.1em;
   text-transform: uppercase;
   color: var(--white-color);
   border: 1px solid var(--white-color);
}

.js-focus-visible :focus:not(.focus-visible) {
   outline: none;
}

.centered {
   text-align: center;
}

.dis-scroll {
   position: fixed;
   left: 0;
   top: 0;
   overflow: hidden;
   width: 100%;
   height: 100vh;
   overscroll-behavior: none;
}

.page--ios .dis-scroll {
   position: relative;
}